___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-background-color: #bbdce5;
    --secondary-background-color: #eceedf;
    --button-background-color: #d9c4b0;
    --accent-color: #cfab8d;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: none;
}

html,
input,
button {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--primary-background-color);
    gap: 2rem;
    min-height: 100dvh;
    min-width: 100dvw;
}

form {
    padding: 1em;
}

input[type="text"],
button {
    font-size: 1.5rem;
    padding: 0.3em;
    background-color: var(--button-background-color);
}

input {
    outline: none;
}

input:focus {
    box-shadow: inset 0 -5px var(--accent-color);
    caret-color: var(--accent-color);
}

button {
    cursor: pointer;
    border: 1px solid black;
    border-radius: 5px;
}

.card {
    width: 40%;
    background-color: var(--secondary-background-color);
    border-radius: 1rem;
    box-shadow: 0 0 5px 5px var(--accent-color);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card p {
    padding: 1rem;
}

h1,
h2 {
    font-size: 2rem;
    padding: 0.5em;
    text-align: center;
}

.state {
    font-weight: 600;
    font-style: italic;
}

.state {
    font-size: 1rem;
}

.emoji {
    font-size: 5rem;
}
